<template>
  <div class="stn-rpt-wrapper container">
    <div v-if="loading" class="loading-overlay">
      <div class="overlay-content"><img src="@/assets/img/loading.gif" style="width: 60px" /></div>
    </div>
    <Header />
    <div class="content-wrapper">
      <div class="filter-toggle-wrapper" ref="toggleFilter">
        <span>
          <button class="filter-toggle" @click="toggleFilter()" :class="isFilterActive ? 'collasped' : ''"><fa :icon="['fas', 'sliders']"></fa></button>
        </span>
        <div class="filter-wrapper" :class="[isFilterActive ? 'slide-in' : 'slide-out']">
          <div class="filter-content-wrapper">
            <popup class="filter-popup-wrapper stores-popup">
              <template #header>
                <button class="filter-btn">
                  <!--<fa icon="store" />--> <span id="store-selection">{{ selectedCompanies.companyName }}</span><fa icon="angle-down" />
                </button>
              </template>
              <template #content>
                <div class="display-selection-wrapper">
                  <span v-for="company in companies" :key="company.id">
                    <input type="radio" name="store_type" :id="company.id" :value="company" v-model="selectedCompanies" @change="selectedStore" />
                    <fa class="checkmark" icon="check" />
                    <label :for="company.id">{{ company.companyName }}</label>
                  </span>
                </div>
              </template>
            </popup>

            <DateType 
              @changeDateType="dateType = $event"
              @changeStartDate="startDate = $event"
              @changeEndDate="endDate = $event"
              :currentStartDate="startDate"
              :currentEndDate="endDate" />

            <Transition name="fade">
              <p v-if="invalidDateRange" class="invalid-search-msg">
                <fa icon="triangle-exclamation" />
                <span>Invalid date range</span>
              </p>
            </Transition>
            
            <div class="filter-header">
              <div v-if="dateType == 'Single Date' || dateType == 'Today'" class="calendar-wrapper">
                <DatePicker @changeDateValue="startDate = $event" :defaultDate="startDate" labelEnableText="Date" />
              </div>
              <div v-if="dateType == 'Date Range' || dateType == 'This Week'" class="calendar-wrapper">
                <DatePicker @changeDateValue="startDate = $event" :defaultDate="startDate" :startDate="endDate" labelEnableText="From" />
                <DatePicker @changeDateValue="endDate = $event" :defaultDate="endDate" :startDate="startDate" :alwaysStartDate="true" labelEnableText="To" />
              </div>
              <button class="search-btn btn btn-square" @click="searchWithFilter"><fa icon="magnifying-glass"></fa></button>
            </div>
          </div>
        </div>
      </div>
      <div class="content" v-bind:style="{ height: 'calc(' + windowHeight + 'px - 90px)' }">
        <div v-if="!empty && stationSummary.length <= 0" class="no-results"><fa :icon="['fas', 'magnifying-glass']" />No search results found</div>
        <DataTable
          v-if="!empty && stationSummary.length > 0"
          id="rpt-table"
          class="tb-wrapper"
          v-model:expandedRows="state.expandedRows"
          dataKey="station"
          :value="stationSummary"
          :exportFilename="fileName"
          ref="dt"
        >
          <template #header>
            <div class="table-header-wrapper">
              <div class="table-header">
                <h3>{{ $t('header.' + this.$route.name) }} - {{ storeName }}</h3>
                <p v-if="singleDateActive">As of date {{ formatDate(rptStartDate) }}</p>
                <p v-else-if="dateRangeActive">As of date {{ formatDate(rptStartDate) }} - {{ formatDate(rptEndDate) }}</p>
              </div>
              <div class="table-btn-group">
                <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-csv"></fa></button>
              </div>
            </div>
          </template>

          <Column field="station" header="Stn#">
            <template #body="slotProps">
              <p class="first-col">{{ slotProps.data.station }}</p>
              <a class="more-info-btn" @click.stop="onExpandRow(slotProps.data.station)">
                <span :class="[state.expandedRows[slotProps.data.station] ? 'expand' : '']">
                  <button class="expand-btn"><fa icon="caret-right" /></button>
                </span>
                {{ slotProps.data.station }}
              </a>
            </template>
          </Column>
          <Column class="portrait-col" field="cash" header="Cash">
            <template #body="slotProps">{{ formatPrice(slotProps.data.cash) }}</template>
          </Column>
          <Column field="footstamp" header="F.Stamp">
            <template #body="slotProps">{{ formatPrice(slotProps.data.footstamp) }}</template>
          </Column>
          <Column field="ebtcash" header="EBT">
            <template #body="slotProps">{{ formatPrice(slotProps.data.ebtcash) }}</template>
          </Column>
          <Column class="portrait-col" field="credit_card" header="Credit">
            <template #body="slotProps">{{ formatPrice(slotProps.data.credit_card) }}</template>
          </Column>
          <Column field="debit_card" header="Debit">
            <template #body="slotProps">{{ formatPrice(slotProps.data.debit_card) }}</template>
          </Column>
          <Column field="gift" header="Gift Card">
            <template #body="slotProps">{{ formatPrice(slotProps.data.gift) }}</template>
          </Column>
          <Column field="check" header="Check">
            <template #body="slotProps">{{ formatPrice(slotProps.data.check) }}</template>
          </Column>
          <Column field="other" header="Other">
            <template #body="slotProps">{{ formatPrice(slotProps.data.other) }}</template>
          </Column>
          <Column field="redemption" header="Redeemed">
            <template #body="slotProps">{{ formatPrice(slotProps.data.redemption) }}</template>
          </Column>
          <Column class="portrait-col total" field="total" header="Total">
            <template #body="slotProps">{{ formatPrice(slotProps.data.total) }}</template>
          </Column>
          <Column field="tax" header="Tax"><template #body="slotProps">{{ formatPrice(slotProps.data.tax) }}</template></Column>
          <Column field="refund" header="Refund"><template #body="slotProps">{{ formatPrice(slotProps.data.refund) }}</template></Column>

          <template #expansion="slotProps">
            <div class="more-info-wrapper">
              <p class="landscape"><span class="bold-600">Foodstamp</span> {{ formatPrice(slotProps.data.footstamp) }}</p>
              <p class="landscape"><span class="bold-600">Debit Card</span> {{ formatPrice(slotProps.data.debit_card) }}</p>
              <p class="landscape"><span class="bold-600">Gift Card</span> {{ formatPrice(slotProps.data.gift) }}</p>
              <p class="landscape"><span class="bold-600">Check</span> {{ formatPrice(slotProps.data.check) }}</p>
              <p class="landscape"><span class="bold-600">Others</span> {{ formatPrice(slotProps.data.other) }}</p>
              <p><span class="bold-600">Redemption</span> {{ formatPrice(slotProps.data.redemption) }}</p>
              <p><span class="bold-600">Tax</span> {{ formatPrice(slotProps.data.tax) }}</p>
              <p><span class="bold-600">Refund</span> {{ formatPrice(slotProps.data.refund) }}</p>
            </div>
          </template>

          <ColumnGroup type="footer">
            <Row>
              <Column class="dt-footer" footer="Total" />
              <Column class="dt-footer" :footer="formatPrice(station.total_cash)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_footstamp)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_ebtcash)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_credit_card)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_debit_card)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_gift_card)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_check)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_others)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_redemption)" />
              <Column class="dt-footer" :footer="formatPrice(station.total)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_tax)" />
              <Column class="dt-footer" :footer="formatPrice(station.total_refund)" />
            </Row>
          </ColumnGroup>

          <template #footer>
            <div class="mobile-dt-footer">
              <label class="bold-600">Total</label>
              <ul>
                <li>Cash: {{ formatPrice(station.total_cash) }}</li>
                <li>Foodstamp: {{ formatPrice(station.total_footstamp) }}</li>
                <li>EBT: {{ formatPrice(station.total_ebtcash) }}</li>
                <li>Credit Card: {{ formatPrice(station.total_credit_card) }}</li>
                <li>Debit Card: {{ formatPrice(station.total_debit_card) }}</li>
                <li>Gift Card: {{ formatPrice(station.total_gift_card) }}</li>
                <li>Check: {{ formatPrice(station.total_check) }}</li>
                <li>Others: {{ formatPrice(station.total_others) }}</li>
                <li>Redemption: {{ formatPrice(station.total_redemption) }}</li>
                <li>Total: {{ formatPrice(station.total) }}</li>
                <li>Tax: {{ formatPrice(station.total_tax) }}</li>
                <li>Refund: {{ formatPrice(station.total_refund) }}</li>
              </ul>
            </div>
          </template>
        </DataTable>

        <div class="extra-info-wrapper" v-if="stationSummary.length > 0">
          <div>
            <p class="bold-600">Extra Information</p>
            <ul class="extra-info">
              <li>Cash Out: {{ formatPrice(station.total_cashout) }}</li>
              <li>Gift Certificate: {{ formatPrice(station.total_cpay) }}</li>
              <li>Coupon: {{ formatPrice(station.total_coupon) }}</li>
              <li>WIC: {{ formatPrice(station.total_wic) }}</li>
              <li>CVV: {{ formatPrice(station.total_wic) }}</li>
              <li>Number of Trans: {{ formatNumber(station.total_trn) }}</li>
              <li>S.Credit (Applied): {{ formatPrice(station.total_store_credit) }}</li>
              <li>S.Credit (Issued): {{ formatPrice(station.total_store_credit_issued) }}</li>
              <li>Deposit (Applied): {{ formatPrice(station.total_deposit) }}</li>
              <li>Deposit (Issued): {{ formatPrice(station.total_deposit_issued) }}</li>
              <li>Cancel: {{ formatPrice(station.total_void_amt) }}</li>
            </ul>
          </div>
          <div class="doughnut-chart">
            <VueApexCharts :options="setDoughnutChartOptions" :series="chartTotalAmount" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { reactive } from "vue";
import axios from "axios";
import moment from "moment";
import Header from "@/components/Header.vue";
import Popup from "@/components/Popup.vue";
import DatePicker from "@/components/DatePicker.vue";
import DateType from "@/components/DateType.vue";

export default {
  components: {
    Header,
    Popup,
    DatePicker,
    DateType
  },
  setup() {
    const state = reactive({
      expandedRows: {},
    });

    const onExpandRow = (id) => {
      const checkProp = id in state.expandedRows;
      if (checkProp) {
        delete state.expandedRows[id];
      } else {
        state.expandedRows[id] = true;
      }
      state.expandedRows = { ...state.expandedRows };
    };

    return {
      state,
      onExpandRow,
    };
  },
  data() {
    return {
      windowHeight: window.innerHeight,
      isFilterActive: true,
      empty: true,
      companies: [
      {
        id: 1027,
        companyName: "Apple Supermarket 1"
        },
        {
        id: 1221,
        companyName: "Apple Supermarket 2"
        },
      ],
      selectedCompanies: [],
      storeName: "",
      loading: false,
      station: [],
      stationSummary: [],
      dateType: "Date Range",
      startDate: "",
      endDate: "",
      rptStartDate: "",
      rptEndDate: "",
      invalidDateRange: false,
      singleDateActive: false,
      dateRangeActive: false
    };
  },
  mounted() {
    // this.companies = JSON.parse(localStorage.getItem("companies"));

    this.selectedCompanies = this.companies[0];

    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });
  },
  // beforeMount() {
  //   window.addEventListener('click', this.close);
  // },
  // unmounted() {
  //   window.removeEventListener('click', this.close);
  // },
  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },
  methods: {
    toggleFilter() {
      this.isFilterActive = !this.isFilterActive;
    },
    // close(e) {
    //   if (!this.$refs.toggleFilter.contains(e.target)) {
    //     this.isFilterActive = false;
    //   }
    // },
    formatPrice(value) {
      if (!value || value == 0) {
        return "0.00";
      }
      let val = (value / 1).toFixed(2).replace(",", ".");
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatNumber(value) {
      if (!value) {
        return 0;
      }
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatDate(date) {
      return moment(date).format("MMM DD, yyyy");
    },
    exportCSV() {
      this.$refs.dt.exportCSV();
    },
    selectedStore() {
      var value = "Nothing selected",
        selected = document.querySelector('input[name="store_type"]:checked'),
        selection = document.querySelector("#store-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    searchWithFilter() {
      this.loading = true;
      this.isFilterActive = false;
      this.station = [];
      this.stationSummary = [];
      this.storeName = this.selectedCompanies.companyName;

      this.rptStartDate = "";
      this.rptEndDate = ""

      if (this.dateType == 'Single Date' || this.dateType == 'Today'){
        this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
        this.rptEndDate = moment(this.startDate).format("YYYY-MM-DD");
        this.singleDateActive = true;
        this.dateRangeActive = false;
      } else if (this.dateType == 'Date Range' || this.dateType == 'This Week'){
        this.dateRangeActive = true;
        this.singleDateActive = false;
        const days = Math.round((this.endDate - this.startDate) / (1000 * 60 * 60 * 24));

        if (this.startDate && this.endDate && days >= 0) {
          this.invalidDateRange = false;
          this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
          this.rptEndDate = moment(this.endDate).format("YYYY-MM-DD");
        } else if (days < 0) {
          this.loading = false;
          this.isFilterActive = true;
          this.invalidDateRange = true;
          this.rptStartDate = "";
          this.rptEndDate = "";
        } else {
          this.loading = false;
          this.rptStartDate = 0;
          this.rptEndDate = 0;
        }
      }
      axios
        .get("https://cloudbackofficereportapidev.azurewebsites.net/Report/Station", {
          headers: {
            marketid: this.selectedCompanies.id,
          },
          params: {
            sDate: this.rptStartDate,
            eDate: this.rptEndDate,
            time_1: this.time1,
            time_2: this.time2,
          },
        })
          .then((response) => {
            this.station = response.data.datas;
            this.stationSummary = response.data.datas.salesSummaries;
          })
          .catch((error) => console.log(error.response))
          .finally(() => {
            this.loading = false;
            this.empty = false;
        });
    },
  },
  computed: {
    fileName() {
      let excelName = this.$route.name + " " + this.startDate + "-" + this.endDate;
      return excelName;
    },
    setDoughnutChartOptions() {
      return {
        chart: {
          type: "donut",
          fontFamily: 'Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif',
        },
        labels: ["Cash", "Food Stamp", "EBT Cash", "Credit Card", "Debit Card", "Gift Card", "Check", "Other"],
        colors: ["#02bffd", "#f86e51", "#04a7bb", "#feba32", "#04ad20", "#d3d4d6", "#2791e4", "#bd5eff"],
        dataLabels: {
          formatter: function (val) {
            return val.toFixed(2) + "%";
          },
          style: {
            fontSize: "15px",
          },
        },
        legend: {},
        tooltip: {
          // enabled: false
        },
        states: {
          hover: {
            filter: {
              type: "none",
            },
          },
        },
        plotOptions: {
          pie: {
            donut: {
              size: "55%",
              labels: {
                show: true,
                value: {
                  fontSize: "16px",
                },
                total: {
                  show: true,
                  label: "Total",
                  color: "#373d3f",
                  formatter: function (w) {
                    return w.globals.seriesTotals
                      .reduce((a, b) => {
                        return a + b;
                      }, 0)
                      .toFixed(2)
                      .toString()
                      .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
                  },
                },
              },
            },
          },
        },
      };
    },
    chartTotalAmount() {
      let total = 0;
      total = [
        this.station.total_cash,
        this.station.total_footstamp,
        this.station.total_ebtcash,
        this.station.total_credit_card,
        this.station.total_debit_card,
        this.station.total_gift_card,
        this.station.total_check,
        this.station.total_others,
      ];
      return total;
    },
  },
};
</script>
